import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import classes from './Layout.module.css';
import Toolbar from '../Navigation/Toolbar';
import SideDrawer from '../Navigation/SideDrawer/SideDrawer';

class Layout extends Component {

    state = {
        showSideDrawer: false
    }

    sideDrawerHandler = () => {
        this.state.showSideDrawer ? this.setState({showSideDrawer: false}) : this.setState({showSideDrawer: true});
    }
    
    render() {
        return (
            <Aux>
                <Toolbar clicked={this.sideDrawerHandler}/>
                <SideDrawer show={this.state.showSideDrawer} clicked={this.sideDrawerHandler}/>
                <main className={classes.Content}>
                    {this.props.children}
                </main>
            </Aux>
        )
    }
}

export default Layout;
